import "../../document.css";
import React from "react";
export default () => (
  <div style={{ padding: "1rem" }}>
    <p>Spēkā stāšanās datums: 2020. gada 30. maijs.</p>

    <h3>Paziņojums par konfidencialitāti</h3>

    <h4 id="overview">Pārskats</h4>

    <p>
      Šī konfidencialitātes paziņojuma mērķis ir izskaidrot, kā ZĪME.lV kā datu
      pārvaldītājs var vākt, saglabāt, apstrādāt, koplietot un pārsūtīt Tavus
      personas datus, kad Tu apmeklē mūsu vietnes vai izmanto mūsu pakalpojumus.
      Šis konfidencialitātes paziņojums attiecas uz Taviem personas datiem, kad
      apmeklē vietnes vai izmanto pakalpojumus, un neattiecas uz tiešsaistes
      vietnēm vai pakalpojumiem, kas mums nepieder vai kuru mēs nekontrolējam,
      ieskaitot citu lietotāju vietnes vai pakalpojumus.
    </p>

    <p>
      Šis paziņojums par konfidencialitāti ir paredzēts, lai palīdzētu Tev iegūt
      informāciju par mūsu privātuma praksi un palīdzētu izprast Tavu privātuma
      izvēli, kad izmanto mūsu vietnes un pakalpojumus. Šis paziņojums par
      konfidencialitāti var tikt papildināts ar papildu paziņojumiem atkarībā no
      attiecīgajām vietnēm un pakalpojumiem. Papildu informācija ir atrodama
      paziņojumā par sīkdatnēm un analītikas tehnoloģijām.
    </p>

    <p>
      Mēs esam definējuši dažus terminus, kurus mēs izmantojam visā paziņojumā
      par konfidencialitāti.
    </p>

    <h4>Kādus personas datus mēs vācam?</h4>

    <p>
      Mēs apkopojam personas datus par Tevi, kad apmeklē mūsu vietnes vai
      izmantojat mūsu pakalpojumus, tostarp šādus:
    </p>

    <ul>
      <li>
        <b>Reģistrācijas un lietošanas informācija – </b>Kad Tu reģistrējies
        mūsu pakalpojumu izmantošanai, izveidojot kontu, mēs apkopojam personas
        datus pēc nepieciešamības, lai piedāvātu un izpildītu Tevis pieprasītos
        pakalpojumus. Atkarībā no izvēlētajiem pakalpojumiem mēs, iespējams,
        pieprasīsim Tev norādīt savu vārdu, pasta adresi, tālruņa numuru,
        e-pasta adresi un identifikācijas informāciju, lai izveidotu kontu. Mēs,
        iespējams, pieprasīsim Tev sniegt mums papildu personas datus, kad Tu
        izmanto mūsu pakalpojumus.
      </li>

      <li>
        <b>Informācija par darījumiem un pieredzi – </b>Kad Tu izmanto mūsu
        pakalpojumus vai piekļūsti mūsu vietnēm, piemēram, lai veiktu pirkumus
        no tirgotājiem, saņemtu pārskaitījumus, apstrādātu maksājumus vai
        nosūtītu pārskaitījumus citiem lietotājiem, mēs apkopojam informāciju
        par darījumu, kā arī citu informāciju, kas saistīta ar darījumu,
        piemēram, nosūtītā vai pieprasītā summa, par produktiem vai
        pakalpojumiem samaksāto summu, informāciju par tirgotāju, ieskaitot
        informāciju par visiem darījuma pabeigšanai izmantotajiem finansēšanas
        instrumentiem, informāciju par ierīci, tehniskā lietojuma datus un
        ģeogrāfiskās atrašanās vietas informāciju.
      </li>

      <li>
        <b>Dalībnieka personas dati – </b>Kad Tu izmanto mūsu pakalpojumus vai
        piekļūsti mūsu vietnēm, mēs apkopojam personas datus, kurus Tu mums
        sniedz par citiem dalībniekiem, kas saistīti ar darījumu.
        <ul>
          <li>
            <b>Nosūtīt vai saņemt pārskaitījumus: </b>Sūtot vai saņemot
            pārskaitījumus, izmantojot pakalpojumus, mēs apkopojam personas
            datus, piemēram, vārdu, pasta adresi, tālruņa numuru un finanšu
            konta informāciju par dalībnieku, kurš saņem pārskaitījumu no Tevis
            vai sūta pārskaitījumu Tev. Nepieciešamo personas datu apjoms par
            dalībnieku var atšķirties atkarībā no pakalpojumiem, kurus izmanto
            parskaitījuma nosūtīšanai vai saņemšanai.
          </li>
          <li>
            <b>Maksā vai pieprasi reķina apmaksu: </b>Ja Tu izmanto mūsu
            pakalpojumus, lai apmaksātu rēķinu kādam citam, vai ja Tu pieprasi,
            lai lietotājs apmaksā rēķinu Tev, mēs no Tevis apkopojam personas
            datus par konta īpašnieku, piemēram, vārdu, pasta adresi, tālruņa
            numuru, e-pasta adresi, rēķina adresi un konta numuru, uz kuru plāno
            apmaksāt vai pieprasīt maksājumu.
          </li>
          <li>
            <b>Pievienojiet vērtību saviem kontiem: </b>Ja Tu izmanto mūsu
            pakalpojumus, lai pievienotu vērtību savam kontam vai jebkuram citam
            kontam, vai ja Tu lūdz lietotājam pievienot vērtību kādam no šiem
            kontiem, mēs varam vākt personas datus no Tevis par otru pusi vai no
            otras puses par Tevi, lai atvieglotu pieprasījuma izpildi.
          </li>
        </ul>
      </li>
      <li>
        <b>Tavu draugu un kontaktpersonu personas dati – </b>Mums var būt
        vieglāk palīdzēt veikt darījumus ar draugiem un kontaktpersonām, ja
        izvēlēsies savienot savu kontaktu saraksta informāciju ar savu kontu. Ja
        izveidosi konta savienojumu starp ierīci vai sociālo mediju platformu un
        kontu, mēs izmantosim Tavu kontaktu saraksta informāciju (piemēram,
        vārdu, adresi, e-pasta adresi), lai uzlabotu Tavu pieredzi, izmantojot
        mūsu pakalpojumus.
      </li>
      <li>
        <b>
          Personas dati, kurus Tu izvēlies sniegt mums, lai iegūtu papildu
          pakalpojumus vai īpašus tiešsaistes pakalpojumus –{" "}
        </b>
        Ja Tu pieprasi noteiktu vietnes funkciju vai piedalāties tajā, vai
        pieprasi uzlabotus pakalpojumus vai citas izvēles funkcijas, mēs varam
        vākt no Tevis papildu informāciju. Apkopošanas laikā mēs Tev sniegsim
        atsevišķu paziņojumu, ja šo personas datu izmantošana atšķiras no šajā
        privātuma paziņojumā norādītajiem izmantošanas veidiem.
      </li>
      <li>
        <b>
          Personas dati par Tevi, ja izmanto pakalpojumu “Izrakstīšanās bez
          ZĪME.LV konta” –{" "}
        </b>
        Daži ierobežoti pakalpojumi ir pieejami bez nepieciešamības pieteikties
        vai izveidot kontu, kas tiek dēvēts arī par Izrakstīšanos bez ZĪME.LV
        konta. Mēs apkoposim personas datus, informāciju par jebkuru izmantoto
        finansēšanas instrumentu, informāciju par ierīci, tehniskās lietošanas
        datus un informāciju par ģeogrāfisko atrašanās vietu, kas nepieciešama
        darījuma pabeigšanai. Ja esi konta īpašnieks vai īpasniece un izvēlies
        iesaistīties Izrakstīšanās bez ZĪME.LV konta darījuma, mēs varam apkopot
        informāciju par darījumu un saistīt to ar Tavu kontu kā daļu no mūsu
        atbilstības un analītiskajām operācijām. Ja Tu neesi konta īpašnieks un
        izvēlies iesaistīties Izrakstīšanās bez ZĪME.LV konta darījuma, mēs
        apkoposim un glabāsim visu Tevis sniegto informāciju un izmantosim šādu
        informāciju saskaņā ar šo konfidencialitātes paziņojumu.
      </li>
      <li>
        <b>Personas dati par Tevi no trešo personu avotiem – </b>Mēs iegūstam
        informāciju no trešo pušu avotiem, piemēram, tirgotājiem, datu
        sniedzējiem un kredītbirojiem, ja to atļauj likums.
      </li>
      <li>
        <b>
          Cita informācija, kuru mēs apkopojam, ir saistīta ar Tavu vietņu vai
          pakalpojumu izmantošanu –{" "}
        </b>
        Mēs varam apkopot papildu informāciju no Tevis vai par Tevi, kad Tu
        sazinies ar mums, sazinies ar mūsu klientu atbalsta komandām vai atbildi
        uz aptaujas jautājumiem.
      </li>
    </ul>

    <h4>Kāpēc mēs saglabājam personas datus?</h4>

    <p>
      Mēs glabājam personas datus identificējamā formātā vismazāko laiku, kas
      nepieciešams mūsu juridisko vai normatīvo saistību izpildei un mūsu
      biznesa mērķiem. Mēs varam glabāt personas datus ilgāku laiku, nekā to
      prasa likums, ja tas ir mūsu likumīgajās biznesa interesēs un nav
      aizliegts ar likumu. Ja Tavs konts tiek slēgts, mēs varam veikt pasākumus,
      lai maskētu personas datus un citu informāciju, taču mēs paturam iespēju
      saglabāt un piekļūt datiem tik ilgi, cik nepieciešams, lai ievērotu
      piemērojamos likumus. Mēs turpināsim izmantot un atklāt šādus personiskos
      datus saskaņā ar šo privātuma paziņojumu.
    </p>

    <p>
      Sīkdatnēm, kuras mēs izmantojam, ir noteikts derīguma termiņš; ja vien
      šajā laikā neapmeklē mūsu vietnes vai neizmanto mūsu pakalpojumus,
      sīkdatnes tiek automātiski atspējotas un saglabātie dati tiek izdzēsti.
    </p>
  </div>
);
